.no-js #loader {
  display: none; }

.js #loader {
  display: block;
  position: absolute;
  left: 100px;
  top: 0; }

.se-pre-con {
  position: fixed;
  left: 0px;
  top: 0px;
  width: 100%;
  height: 100%;
  z-index: 9999;
  background: url(images/preloader.gif) center no-repeat #fff; }

.page-id-9 .entry-header {
  display: none; }

.custom-logo-link img {
  max-width: 200px; }

.navbar {
  padding: 1rem;
  background-color: #fff;
  box-shadow: 0px 3px 6px 0px rgba(122, 120, 122, 0.33); }
  .navbar.shrink {
    top: 0 !important;
    height: 70px !important;
    padding: 0.5rem 1rem !important;
    background-color: #fafafa !important;
    transition: all 0.33s ease-in-out; }
    .navbar.shrink .mob-standard-logo {
      height: 70px !important;
      transition: all 0.33s ease-in-out; }

.navbar-expand-md .navbar-nav .nav-link {
  padding-right: 1.125rem;
  padding-left: 1.125rem; }

#page {
  margin-top: 2.25rem; }

