.section {
  z-index: 1;
  position: relative;
  padding-top: 3.375rem;
  padding-bottom: 6.75rem; }

.section-newsletter h2,
.section-newsletter p,
.section-whatever h2,
.section-whatever p {
  color: #fff; }

.section-scroll-to {
  display: flex;
  margin-top: 2.25rem; }

.fixed-bg {
  background-attachment: fixed;
  background-position: center center;
  background-size: cover; }
  .fixed-bg-newsletter {
    background-image: url("/wp-content/uploads/2019/09/bigplane-test-image.jpg"); }
  .fixed-bg::after {
    position: absolute;
    top: 0;
    left: 0;
    z-index: -2;
    content: "";
    width: 100%;
    height: 100%;
    background: #000 none repeat scroll 0 0;
    opacity: 0.5; }

.text-center {
  text-align: center; }

h1, h2, h3, h4, h5, h6 {
  text-transform: uppercase; }

p {
  font-size: 0.875rem;
  line-height: 1.8; }

.section-heading {
  margin-bottom: 3.375rem;
  display: inline-block;
  font-weight: 700;
  padding-bottom: 20px;
  position: relative; }

.homepage__block {
  background-color: #ccc;
  min-height: 400px; }

.no-js #loader {
  display: none; }

.js #loader {
  display: block;
  position: absolute;
  left: 100px;
  top: 0; }

.se-pre-con {
  position: fixed;
  left: 0px;
  top: 0px;
  width: 100%;
  height: 100%;
  z-index: 9999;
  background: url(../images/preloader.gif) center no-repeat #fff; }
